:root {
  --netflix-red: rgb(229, 9, 20);
  --netflix-red-hover: rgb(244, 6, 18);
  --netflix-black: rgb(24, 24, 24);
  --netflix-white: rgb(254, 252, 248);
  --netflix-gray: rgb(163, 163, 163);
  --netflix-background: rgb(24, 24, 24);
  --netflix-red-rgb: 229, 9, 20;
  --netflix-red-hover-rgb: 244, 6, 18;
  --netflix-black-rgb: 24, 24, 24;
  --netflix-white-rgb: 254, 252, 248;
  --netflix-gray-rgb: 163, 163, 163;
  --netflix-black-lg: linear-gradient(90deg, rgb(51, 51, 51) 5%, rgb(24, 24, 24) 95%);
  --netflix-red-lg: linear-gradient(180deg, rgb(229, 9, 20), rgb(219, 5, 16));
}

@keyframes blink {
  from, 50% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fade-out {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes zoom-in {
  0% {
    opacity: 0;
    visibility: hidden;
    transform: scale3d(0.3, 0.3, 0.3);
  }
  50% {
    opacity: 1;
    visibility: visible;
  }
}
@keyframes spin {
  from {
    transform: rotate(0turn);
  }
  to {
    transform: rotate(1turn);
  }
}
@keyframes roll {
  from {
    transform: translate(-100%) rotate(0turn);
  }
  to {
    transform: translate(100%) rotate(10turn);
  }
}
