html {
  font-size: 16px;
  line-height: 1.2;
  scroll-behavior: smooth;
}

body {
  min-height: 100vh;
  font-family: "Poppins", sans-serif;
  background-color: var(--netflix-background);
  color: var(--netflix-white);
  overflow-x: hidden;
}

:focus-visible {
  outline: none;
}

header {
  position: absolute;
  height: 64px;
  left: 0;
  top: 0;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 3;
}

.burger-btn {
  --burger-transition-duration: 0.25s;
  --burger-color: rgba(var(--netflix-white-rgb), 0.8);
  --burger-bg: transparent;
  --burger-color-h: rgba(var(--netflix-black-rgb), 0.9);
  --burger-bg-h: rgba(var(--netflix-white-rgb), 0.9);
  --burger-border-size: 3px;
  --burger-size: 25px;
  --burger-layer-gap: 8px;
  width: calc(var(--burger-size) * 2);
  height: calc(var(--burger-size) * 2);
  position: relative;
  transition-duration: var(--burger-transition-duration);
  cursor: pointer;
}
.burger-btn .burger-circle {
  width: calc(var(--burger-size) * 2);
  height: calc(var(--burger-size) * 2);
  transition-duration: var(--burger-transition-duration);
  position: absolute;
  top: calc(var(--burger-border-size) / 2);
  border-radius: 50%;
  background: var(--burger-bg);
  border: var(--burger-border-size) solid var(--burger-color);
  right: 0;
}
.burger-btn .burger {
  width: var(--burger-size);
  height: var(--burger-border-size);
  transition-duration: var(--burger-transition-duration);
  position: absolute;
  top: var(--burger-size);
  right: calc(var(--burger-size) / 2);
  background-color: var(--burger-color);
  border-radius: var(--burger-border-size);
}
.burger-btn .burger::before, .burger-btn .burger::after {
  border-radius: var(--burger-border-size);
  transition-duration: var(--burger-transition-duration);
  position: absolute;
  width: var(--burger-size);
  height: var(--burger-border-size);
  background-color: var(--burger-color);
  content: "";
  top: calc(var(--burger-layer-gap) * -1);
}
.burger-btn .burger::after {
  top: var(--burger-layer-gap);
}
.burger-btn:hover .burger-circle {
  background: var(--burger-bg-h);
}
.burger-btn:hover .burger, .burger-btn:hover .burger::before, .burger-btn:hover .burger::after {
  background: var(--burger-bg-h);
}
.burger-btn:hover .burger-circle ~ .burger, .burger-btn:hover .burger-circle ~ .burger::before, .burger-btn:hover .burger-circle ~ .burger::after {
  background: var(--burger-color-h);
}

[type=checkbox]#burger-btn {
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
  position: absolute;
}

#burger-btn:checked ~ .burger-btn .burger,
.burger-btn.open .burger {
  background: transparent;
}
#burger-btn:checked ~ .burger-btn .burger:before,
.burger-btn.open .burger:before {
  transform: translateY(var(--burger-layer-gap)) rotateZ(45deg) scaleX(1.25);
}
#burger-btn:checked ~ .burger-btn .burger:after,
.burger-btn.open .burger:after {
  transform: translateY(calc(var(--burger-layer-gap) * -1)) rotateZ(-45deg) scaleX(1.25);
}

.nav-container {
  max-width: 100vw;
  margin: 0 auto;
  height: 64px;
  font-size: 18px;
}
.nav-container .logo img {
  height: 32px;
  max-width: 32px;
  object-fit: cover;
}
@media (min-width: 768px) {
  .nav-container .logo img {
    max-width: unset;
  }
}
.nav-container::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 64px;
  z-index: -1;
  background: rgba(var(--netflix-black-rgb), 0.4);
  filter: blur(3px);
  box-shadow: 0 0 2px 2px rgba(var(--netflix-black-rgb), 0.4);
}
.nav-container .nav {
  transition: right 0.75s ease-out;
}
.nav-container .nav .nav-link {
  color: var(--netflix-white);
  position: relative;
  color: rgba(var(--netflix-white-rgb), 0.8);
}
@media (min-width: 992px) {
  .nav-container .nav .nav-link::after {
    content: "";
    position: absolute;
    bottom: 0;
    height: 3px;
    left: 50%;
    right: 50%;
    background: rgba(var(--netflix-white-rgb), 1);
    transition-duration: 0.25s;
  }
  .nav-container .nav .nav-link:hover::after {
    left: 15%;
    right: 15%;
  }
}
.nav-container .nav .nav-link.active, .nav-container .nav .nav-link:hover {
  font-weight: 600;
  color: rgba(var(--netflix-white-rgb), 1);
}
.nav-container .nav .nav-link.active {
  pointer-events: none;
}
.nav-container .info-btn {
  color: var(--netflix-white);
  text-decoration: none;
}
.nav-container .info-btn.active, .nav-container .info-btn:hover {
  color: rgba(var(--netflix-white-rgb), 1);
}
@media (max-width: 992px) {
  .nav-container .nav {
    position: fixed;
    bottom: 0;
    top: 70px;
    z-index: 4;
    width: 100vw;
    right: -105vw;
    background: var(--netflix-black);
    box-shadow: 0 0 4px 4px rgba(var(--netflix-black-rgb), 0.5);
    border-radius: 4px;
    align-items: center;
  }
  .nav-container .nav .nav-link {
    transition: filter 0.25s ease;
    font-size: 18px;
    font-weight: 500;
    text-align: center;
    margin-bottom: 24px;
  }
  .nav-container .nav .nav-link:hover {
    filter: drop-shadow(4px 4px 20px var(--netflix-red-hover)) contrast(200%);
    text-decoration: none;
  }
  .nav-container #burger-btn:checked ~ .nav {
    right: 0;
  }
}

.main-loader {
  position: fixed;
  z-index: 500;
  inset: 0;
  background: var(--netflix-black);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.main-loader .fluo {
  --fluo-outer-size: 15vmin;
  --fluo-wheel-size: 15%;
  --fluo-inner-bg: rgba(var(--netflix-black-rgb), 0.2);
  --fluo-text-color: var(--netflix-white);
  --fluo-wheel-gradient: linear-gradient(#14ffe9, #ffeb3b, #ff00e0);
  position: relative;
  width: var(--fluo-outer-size);
  height: var(--fluo-outer-size);
  font-size: calc(var(--fluo-outer-size) * 0.275);
  margin: calc(var(--fluo-outer-size) / 6);
  animation: spin 0.5s linear infinite;
  box-shadow: 0 0 16px 16px rgba(0, 0, 0, 0.3);
  border-radius: 50%;
  background: var(--fluo-wheel-gradient);
  color: var(--fluo-text-color);
}
.main-loader .fluo svg {
  position: absolute;
  width: 70%;
  height: 70%;
  margin: 15%;
  color: rgba(18, 18, 18, 0.9);
  animation: spin 0.75s linear reverse infinite;
}
.main-loader .fluo .fluo-color {
  border-radius: 50%;
  background: var(--fluo-wheel-gradient);
  width: 100%;
  height: 100%;
  position: absolute;
}
.main-loader .fluo .fluo-color:nth-child(1) {
  filter: blur(5px);
}
.main-loader .fluo .fluo-color:nth-child(2) {
  filter: blur(10px);
}
.main-loader .fluo .fluo-color:nth-child(3) {
  filter: blur(25px);
}
.main-loader .fluo .fluo-color:nth-child(4) {
  filter: blur(50px);
}
.main-loader .fluo::after {
  content: "";
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  inset: var(--fluo-wheel-size);
  width: calc(100% - 2 * var(--fluo-wheel-size));
  height: calc(100% - 2 * var(--fluo-wheel-size));
  border-radius: 50%;
  background: var(--fluo-inner-bg);
  box-shadow: inset 0 0 16px 16px rgba(0, 0, 0, 0.3);
  text-shadow: 3px 3px 3px rgba(0, 0, 0, 0.3);
  animation: spin 0.5s linear infinite reverse;
}
.main-loader .fluo.fast, .main-loader .fluo::after.fast {
  animation-duration: 0.25s;
}
.main-loader .fluo.faster, .main-loader .fluo::after.faster {
  animation-duration: 0.15s;
}
.main-loader .fluo.slow, .main-loader .fluo::after.slow {
  animation-duration: 0.75s;
}
.main-loader .fluo.slower, .main-loader .fluo::after.slower {
  animation-duration: 1s;
}
.main-loader .fluo[data-percent]::after {
  content: attr(data-percent) "%";
}
.main-loader .fluo[data-percent="100"], .main-loader .fluo.complete {
  animation-duration: 5s;
}
.main-loader .fluo[data-percent="100"]::after, .main-loader .fluo.complete::after {
  animation-duration: 5s;
}
.main-loader .fluo::after {
  box-shadow: none;
  background: rgba(var(--netflix-black-rgb), 0.6);
}
.main-loader .typed {
  margin-right: 8px;
}
.main-loader .load-message {
  text-align: center;
  font-size: 16px;
  font-weight: 600;
}
@media (min-width: 992px) {
  .main-loader .load-message {
    font-size: 20px;
  }
}

.audio-player {
  position: fixed;
  visibility: hidden;
  opacity: 0;
  top: -100vmax;
  right: -100vmax;
}

.mute-sound {
  position: absolute;
  top: 72px;
  right: 16px;
  z-index: 300;
  padding: 8px;
  border-radius: 50%;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 2px solid transparent;
  outline: 1px solid transparent;
  transition-duration: 0.5s;
}
.mute-sound:active, .mute-sound:hover {
  border-color: rgba(var(--netflix-black-rgb), 0.7);
  background: rgba(var(--netflix-white-rgb), 0.7);
  color: var(--netflix-black);
  transform: scale(1.2);
}
.mute-sound:active {
  outline-color: rgba(var(--netflix-red-rgb), 0.7);
}
@media (min-width: 992px) {
  .mute-sound {
    padding: 16px;
    right: 2vw;
    top: 96px;
  }
}
.mute-sound[data-muted=true] [data-muted=false] {
  display: none;
}
.mute-sound[data-muted=false] [data-muted=true] {
  display: none;
}

